* {
  margin: 0;
  padding: 0;
}

.App {
  font-family: sans-serif;
  text-align: center;
}

.mapboxMap {
  height: 100vh;
  width: 100vw;
}

.filters {
  position: absolute;
  top: 10px;
  left: 10px;
  background: white;
  border: soild 1px #444;
  border-radius: 5px;
  z-index: 9999;
}

.filterButtons {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 10px;
}

.filterButton {
  border: solid 1px #777;
  padding: 2px 5px;
}

.filterButton.active {
  background: #444;
  color: white;
}

.list {
  margin-top: 5px;
  list-style: none;
  width: 150px;
}

.listItem {
  font-size: small;
  padding: 5px 15px;
}

.listItem:not(:last-child) {
  border-bottom: solid 1px #ddd;
}

.listItem:hover,
.listItem.active {
  background: rgb(255, 255, 215);
}

.listItem:before {
  content: "";
  display: inline-block;
  width: 6px;
  height: 6px;
  margin-bottom: 1.5px;
  margin-right: 8px;
  border-radius: 50%;
  background: #444;
}

.listItem.red:before {
  background: red;
}

.listItem.blue:before {
  background: blue;
}

.marker {
  display: block;
  padding: 10px;
  border-radius: 50%;
  border: solid 3px white;
  z-index: 9999;
}

.marker.red {
  background: red;
}

.marker.blue {
  background: blue;
}

.marker.active {
  padding: 20px;
}
